<style scoped lang="scss">
* {
	margin: 0;
	padding: 0;
}
#buttonGroup {
	position: fixed;
	display: flex;
	flex-direction: column;
	bottom: 20%;
	right: 0%;
	height: 20%;
	justify-content: space-around;
	button {
		width: 3rem;
		height: 3rem;
		font-size: 0.8rem;
		padding: 10px;
		border: none;
		transition: 0.5s;
		border-radius: 50%;
		&:hover {
			cursor: pointer;
			background: #000;
			color: #fff;
			border: 1px solid #fff;
		}
	}
}
//   PC端
#pcMusic {
	width: 100%;
	height: 100vh;
	overflow: hidden;
	position: relative;
	background: linear-gradient(to bottom, #000000, #000000);
